var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',[_c('v-init-view',[_c('p',{staticClass:"link",attrs:{"slot":"right"},on:{"click":_vm.closeEvent},slot:"right"},[_c('span',{staticClass:"fa fa-close "}),_vm._v("关闭当前页 ")]),_c('div',{staticClass:"container "},[_c('div',{staticClass:"agreement-container"},[_c('h2',{staticClass:"title"},[_vm._v("用户服务协议")]),_c('p',[_vm._v("本协议是您与“e农商城”的所有者“华农（北京）电子商务有限公司”（以下简称为“e农商城”）之间就e农商城所提供的服务及相关事宜所立订的契约，请您仔细阅读本注册协议，在您确认阅读并选择注册后，本协议即构成对双方有约束力的法律文件。")]),_c('p',{staticClass:"bold"},[_vm._v("第一条 e农商城服务条款的确认和接纳")]),_c('p',[_vm._v("1.e农商城的各项电子服务的所有权和运作权归“e农商城”所有。客户同意所有注册协议条款并完成注册程序，才能成为e农商城的正式客户。客户确认：本协议条款是处理双方权利义务的契约，始终有效，法律另有强制性规定或双方另有特别约定的，依其规定。")]),_c('p',[_vm._v("2.客户点击同意本协议的，即视为客户确认自己具有享受e农商城服务、下单购物等相应的权利能力和行为能力，能够独立承担法律责任。")]),_c('p',[_vm._v("3.如果您在18周岁以下，您只能在父母或监护人的监护参与下才能使用e农商城。")]),_c('p',[_vm._v("4.“e农商城”保留在中华人民共和国大陆地区法施行之法律允许的范围内独自决定拒绝服务、关闭客户账户、清除或编辑内容或取消订单的权利。")]),_c('p',{staticClass:"bold"},[_vm._v("第二条 法律管辖和适用")]),_c('p',[_vm._v("本协议的订立、执行和解释及争议的解决均应适用在中华人民共和国大陆地区适用之有效法律（但不包括其冲突法规则）。 如发生e农商城服务条款与适用之法律相抵触时，则这些条款将完全按法律规定重新解释，而其它有效条款继续有效。 如缔约方就本协议内容或其执行发生任何争议，双方应尽力友好协商解决；协商不成时，任何一方均可向有管辖权的中华人民共和国大陆地区法院提起诉讼。")]),_c('p',{staticClass:"bold"},[_vm._v("第三条 e农商城服务")]),_c('p',[_vm._v("1.e农商城通过互联网依法为客户提供互联网信息等服务，客户在完全同意本协议及e农商城规定的情况下，方有权使用e农商城的相关服务。 2.客户必须自行准备如下设备和承担如下开支：")]),_c('p',[_vm._v("  （1）上网设备，包括并不限于电脑或者其他上网终端、调制解调器及其他必备的上网装置；")]),_c('p',[_vm._v("  （2）上网开支，包括并不限于网络接入费、上网设备租用费、手机流量费等。")]),_c('p',{staticClass:"bold"},[_vm._v("第四条 客户信息")]),_c('p',[_vm._v("1.客户应自行诚信向e农商城提供注册资料，客户同意其提供的注册资料真实、准确、完整、合法有效，客户注册资料如有变动的，应及时更新其注册资料。如果客户提供的注册资料不合法、不真实、不准确、不详尽的，客户需承担因此引起的相应责任及后果，并且“e农商城”保留终止客户使用“e农商城”各项服务的权利。")]),_c('p',[_vm._v("2.客户在e农商城进行浏览、下单购物等活动时，涉及客户真实姓名/名称、通信地址、联系电话、电子邮箱等隐私信息的，e农商城将予以严格保密，除非得到客户的授权或法律另有规定，e农商城不会向外界披露客户隐私信息。")]),_c('p',[_vm._v("3.客户注册成功后，将产生客户名和密码等账户信息，您可以根据e农商城规定改变您的密码。客户应谨慎合理的保存、使用其客户名和密码。客户若发现任何非法使用客户账号或存在安全漏洞的情况，请立即通知e农商城并向公安机关报案。")]),_c('p',[_vm._v("4.客户同意，“e